// Box shadow
.shadow {
    box-shadow: 0 4px 16px #e2e2e2;
}

.post-shadow {
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.shadow-bottom {
    box-shadow: 0 1px 15px rgba(0, 0, 0, 0.1);
}

.shadow-inside {
    box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.1);
}

.shadow-inside-top {
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.08);
}

.shadow-inside-bottom {
    box-shadow: inset 0 -1px 2px rgba(0, 0, 0, 0.08);
}

// Predefined text helpers
.text-xs,
.text-xs span {
    font-size: 30px;
    line-height: 30px;
}

.text-medium-light {
    font-size: 50px !important;
    font-weight: 100;
    line-height: 1.1;
}

.text-medium {
    font-size: 50px !important;
    font-weight: 800;
    line-height: 1.1;
    margin-bottom: 20px;
}

.text-md,
.text-md span,
.text-md span span {
    font-size: 74px !important;
    line-height: 86px !important;
}

.text-lg-light,
.text-lg-light span,
.text-lg-light span span {
    font-size: 90px !important;
    line-height: 100px !important;
    font-weight: 120 !important;
}

.text-lg,
.text-lg span,
.text-lg span span {
    font-size: 90px;
    line-height: 100px;
    letter-spacing: -.02em;
    font-weight: 700;
}

.text-lg-x2,
.text-lg-x2 span {
    color: #ffffff;
    font-size: 120px;
    line-height: 140px;
    margin-bottom: 10px;
}

.text-lg-x3,
.text-lg-x3 span {
    color: #ffffff;
    font-size: 200px;
    line-height: 220px;
}

.text-xl,
.text-xl span {
    font-size: 100px;
    line-height: 110px;
}

.text-small {
    font-size: 16px;
    font-weight: 800;
    line-height: 1.1;
}

.text-light,
.text-light h1,
.text-light h2,
.text-light h3,
.text-light h4,
.text-light h5,
.text-light h6,
.text-light .lead,
.text-light p,
.text-light a:not(.btn),
.text-light a:not(.btn):hover:not(.btn-light),
.text-light i,
.text-light li,
.text-light label,
.text-light div:not(.alert),
.text-light span:not(.btn-label) {
    color: #fff !important;
}

.text-dark,
.text-dark h1,
.text-dark h2,
.text-dark h3,
.text-dark h4,
.text-dark h5,
.text-dark h6,
.text-dark p,
.text-dark .lead,
.text-dark a:not(.btn),
.text-dark a:not(.btn-danger),
.text-dark a:not(.btn):hover:not(.btn-light),
.text-dark i,
.text-dark li,
.text-dark label,
.text-dark div:not(.alert),
.text-dark span:not(.btn-label) {
    color: #111 !important;
}

.text-grey,
.text-grey h1,
.text-grey h2,
.text-grey h3,
.text-grey h4,
.text-grey h5,
.text-grey h6,
.text-grey .lead,
.text-grey p,
.text-grey a:not(.button),
.text-grey a:not(.btn-light):hover,
.text-grey i,
.text-grey li,
.text-grey label,
.text-grey div:not(.alert),
.text-grey span:not(.btn-label) {
    color: #ccc;
}

.text-white {

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
